@import '../_mixins', '../_variables';

footer {
    background-color: $GrayColor;
    padding: 20px 50px;
    @include flexDisplay(space-between);
    align-items: center;
    * {
        color: $TextColor; }
    .socials {
        a {
            margin-right: $lgSpace;
            svg {
                font-size: 2em; }
            &:hover {
                svg > * {
                    color: $PrimaryColor;
                    transition: 0.2s linear; } } } }
    button {
        background-color: transparent;
        cursor: pointer;
        outline: none;
        padding: $xsSpace;
        @include createBorder(1px, $TextColor);
        transition: 0.2s linear;
        &:hover {
            border-color: $PrimaryColor;
            svg > * {
                color: $PrimaryColor; } } } }
